.services {
  background-color: var(--main-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2% 4%;
}

.services h2 {
  margin-bottom: 1rem;
  color: var(--secondary-color);
}

.services .description {
  width: 38%;
  text-align: center;
  margin-bottom: 3rem;
}

.services .services-items {
  display: grid;
  grid-template-areas: "1 2 3";
  height: 65%;
}

.services .services-items .photography {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10%;
  border-right: 1px solid var(--text);
  border-bottom: 1px solid var(--text);
}

.services .services-items .photography svg {
  font-size: 48px;
  margin-bottom: 1rem;
  color: var(--secondary-color);
}

.services .services-items .photography h3 {
  font-size: 23px;
  margin-bottom: 1rem;
  color: var(--secondary-color);
}

.services .services-items .photography p {
  width: 85%;
}

