/* font-family: 'Jost', sans-serif; */
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@200;300;400;500;600;700;800;900&display=swap");

/* font-family: 'Eczar', sans-serif; */
@import url("https://fonts.googleapis.com/css2?family=Eczar:wght@500;600;700;800&display=swap");

* {
  padding: 0;
  border: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --main-color: #1b1b1b;
  --secondary-color: #fff;
  --text: #999;
}

body {
  font-family: "Jost", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--text);
  transition: all 0.4s;
  overflow-x: hidden;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--main-color);
  border-radius: 5px;
}

body::-webkit-scrollbar {
  width: 12px;
  background-color: var(--text);
}

h1 {
  font-family: "Jost", sans-serif;
  font-size: 65px;
  text-transform: uppercase;
  line-height: 1.2em;
  font-weight: 600;
}

h2 {
  font-size: 36px;
  font-family: "Jost", sans-serif;
}

section {
  width: 100%;
  height: 100vh;
  padding-top: 1%;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

@keyframes show {
  to {
    transform: translate(0px);
    opacity: 1;
  }
}

@keyframes showImage {
  to {
    opacity: 1;
  }
}

@keyframes showDisplay {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes showCard {
  from {
    opacity: 0;
    right: -50px;
  }
  to {
    opacity: 1;
    right: 0;
  }
}

@keyframes showCardResponsive {
  from {
    opacity: 0;
    top: 30px;
  }
  to {
    opacity: 1;
    top: 0;
  }
}