.about .container-about {
  display: flex;
  justify-content: space-around;
  padding: 0 3%;
}

.about .container-about .about-info {
  max-width: 35vw;
  margin-top: 3rem;
}

.about .container-about .about-info h2 {
  color: var(--main-color);
  margin-bottom: 2rem;
}

.about .container-about .about-info p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.7em;
  color: var(--text);
  margin-bottom: 20px;
}

.about .container-about .about-info ul {
  margin-bottom: 1.5rem;
}

.about .container-about .about-info ul li {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 18px;
  margin-bottom: 5px;
}

.about .container-about .about-info ul li svg {
  font-size: 40px;
  color: var(--main-color);
}

.about .container-about .about-info .underline {
  width: 100%;
  height: 2px;
  margin-bottom: 1.5rem;
  background-color: var(--main-color);
  opacity: 0.1;
}

.about .container-about .about-info .about-bottom {
  display: flex;
  gap: 35px;
}

.about .container-about .about-info .about-bottom img {
  width: 90px;
}

.about .container-about .about-info .about-bottom .about-name {
  color: var(--main-color);
  font-size: 21px;
  font-weight: 600;
}

.about .container-about .about-info .about-bottom .about-name span {
  display: block;
  margin-top: 10px;
  color: var(--text);
  font-size: 17px;
  font-weight: 400;
}

.about .container-about .about-image {
  margin-top: 3rem;
  max-height: 39rem;
  max-width: 30rem;
  position: relative;
}

.about .container-about .about-image .image {
  padding: 0 30px 30px 15px;
  position: relative;
}

.about .container-about .about-image .image::after {
  content: "";
  width: 50%;
  position: absolute;
  top: -7%;
  bottom: 60%;
  left: -3%;
  background-image: url("../../../public/images/contact/dots.png");
  background-repeat: repeat;
  z-index: -1;
}

.about .container-about .about-image .image::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 0;
  left: 45px;
  bottom: 0;
  height: 94%;
  background-color: #2b2b2b;
}

.about .container-about .about-image .image img {
  width: 400px;
  position: relative;
  z-index: 2;
  background: #1b1b1b;
  transition: all ease 0.4s;
}

.about .container-about .about-image .image img:hover {
  transform: scale(0.95);
}
