@media (max-width: 760px) {
  header .container {
    justify-content: left;
  }

  header .container .logo {
    margin-left: 5%;
  }

  header .container .menu {
    display: block;
    position: absolute;
    right: 2rem;
    font-size: 35px;
    cursor: pointer;
    transition: all ease 0.4s;
  }

  header .container .menu:hover {
    color: var(--secondary-color);
  }

  header .container .menu.active ~ ul {
    opacity: 1;
    pointer-events: auto;
    transform: scale(1);
    top: 5rem;
    right: 2rem;
  }

  header .container ul {
    position: absolute;
    right: 2rem;
    top: 3rem;
    flex-direction: column;
    background-color: var(--main-color);
    border-radius: 8px;
    border: 1px solid #ffffff36;
    box-shadow: 4px 4px 6px #0000004d;
    transition: all ease 0.3s;
    transform: scale(0.7);
    pointer-events: none;
    opacity: 0;
  }

  header .container ul li {
    display: flex;
    align-items: center;
    justify-items: center;
    width: 100%;
    height: 40px;
  }

  header .container ul li a {
    width: 100%;
    margin: 0;
    padding: 1rem 2rem;
    font-size: 20px;
    text-align: center;
  }
}

@media (max-width: 400px) {
  header .container ul {
    right: 2rem;
    top: 3rem;
  }
}
